<template>
  <div class="head">
    <van-nav-bar :title="title" left-text="" :class="{'active': headerActive}" left-arrow>
      <template #left></template>
      <template #right>
        <div class="wallet-block">
          <span v-if="address" class="address flex-center">***{{address.substring(address.length - 4, address.length)}}</span>
          <span v-else class="address flex" @click="initWallet">{{$t('web3.连接钱包')}}</span>
        </div>
      </template>
    </van-nav-bar>
  </div>
<!--  <Footer></Footer>-->
</template>

<script>
import { reactive, toRefs, computed, watch } from "vue";
// import Footer from "@/components/Footer";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
export default {
  components: {},
  props: {
    Headtype: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
  },
  setup(props) {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      open: false,
      Headtype: props.Headtype,
      headerActive: 0,
      title: props.title,
      local: getLang().toLowerCase() || "zh-cn",
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      initContract: () => {
        data.initData()
      },
      initData: () => {
      },
      initWallet: () => {
        let ballance = web3Data.initWeb3()
        if (ballance) {
          location.reload()
        }
      },
      openUrl: (url) => {
        window.location.href = url;
      }
    })
    document.addEventListener('scroll', function () {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
  .active{
    background: #FFFFFF !important;
  }
  .wallet-block{
    padding: 4px 0 4px 10px;
    width: 108px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .address{
      overflow: hidden;
      white-space:nowrap;
      font-size: 14px;
      color: #111111;
      margin-left: 10px;
    }
  }
  .menu-Block{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
    z-index: 600;
    .menu-leftBlock{
      position: fixed;
      left: 0;
      top: 0;
      bottom: 0;
      background: #fff;
      right: 40%;
      .menu-item{
        padding: 14px 20px;
        .menu-img{
          width: 22px;
          height: 22px;
        }
        .menu-tit{
          font-size: 14px;
          color: #222222;
          margin-left: 20px;
        }
      }
    }
  }
  .back{
    width: 10px !important;
    height: 18px;
  }
</style>
