<template>
  <div class="index-bg zui-management">
    <HeadTop :Headtype="Headtype" :title="$t('management.合約管理')"></HeadTop>
    <div class="p15" style="overflow: hidden; padding-bottom: 30px;">
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.最小兌換數量') }}</div>
        <van-field
          type="number"
          v-model="min"
          center
          clearable
          :placeholder="$t('management.輸入最小兌換數量')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setMinAmount">保存</van-button>
          </template>
        </van-field>
      </div>
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.單筆兌換限額') }}: {{ maxAmount }}</div>
        <van-field
            type="number"
            v-model="max"
            center
            clearable
            :placeholder="$t('management.輸入單筆兌換限額')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setMaxAmount">保存</van-button>
          </template>
        </van-field>
      </div>
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.接收地址') }}</div>
        <van-field
            v-model.trim="recivedAddress"
            center
            clearable
            :placeholder="$t('management.輸入接收地址')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setReceiveAddress">保存</van-button>
          </template>
        </van-field>
      </div>
      <div class="manage-box">
        <div class="manage-title">{{ $t('management.手續費占比') }}</div>
        <van-field
          type="number"
          v-model="rate"
          center
          clearable
          :placeholder="$t('management.手續費占比提示')">
          <template #button>
            <van-button size="small" type="primary" style="width: 60px; font-size: 13px;" @click="setRate">保存</van-button>
          </template>
        </van-field>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'vant';
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import BigNumber from "bignumber.js";
import config from "@/utils/config";
import seeleAbi from "@/utils/seeleAbi";
import tokenAbi from "@/utils/abi";
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      Headtype: 'back',
      maxAmount: '0.00',
      min: '',
      max: '',
      recivedAddress: '',
      rate: '',
      denominator: '',
      seeleContract: null,
      seeleTokenContract: null,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      initContract: () => {
        data.seeleContract = new data.web3.eth.Contract(seeleAbi, config.SeeleAddress)
        data.seeleTokenContract = new data.web3.eth.Contract(tokenAbi, config.TokenAddress)
        data.initData()
      },
      initData: () => {
        data.min = ''
        data.max = ''
        data.recivedAddress = ''
        data.gasnum = ''
        data.getDenominator()
        data.getMaxAmount()
      },
      getDenominator: async () => {
        // let rate = await data.seeleContract.methods.rate().call()
        let denominator = await data.seeleContract.methods.denominator().call()
        // console.log(rate)
        // console.log(denominator)
        data.denominator = denominator
      },
      getMaxAmount: async () => {
        let maxAmount = await data.seeleContract.methods.maxAmount().call()
        let decimals = await data.seeleTokenContract.methods.decimals().call()
        data.maxAmount = Number(maxAmount/Math.pow(10, decimals)).toFixed(2)
      },
      setMinAmount: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.min) {
          Toast(data.$t('management.輸入最小兌換數量'))
          return
        }
        if (Number(data.min) < 0) {
          Toast(data.$t('management.最小數量需大於0'))
          return
        }
        let decimals = await data.seeleTokenContract.methods.decimals().call()
        let minAmount = new BigNumber(data.min).multipliedBy(Math.pow(10, decimals)).toFixed()
        const toast0 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.seeleContract.methods.setMinAmount(
          minAmount
        ).send({
          from: data.address
        }).then(receipt => {
          toast0.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
          console.log(receipt)
        }).catch(err => {
          toast0.clear()
          Toast(data.$t('management.保存失敗'))
          console.error(err)
        })
      },
      setMaxAmount: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.max) {
          Toast(data.$t('management.輸入單筆兌換限額'))
          return
        }
        if (Number(data.max) < 0) {
          Toast(data.$t('management.最小數量需大於0'))
          return
        }
        let decimals = await data.seeleTokenContract.methods.decimals().call()
        let maxAmount = new BigNumber(data.max).multipliedBy(Math.pow(10, decimals)).toFixed()
        const toast0 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.seeleContract.methods.setMaxAmount(
            maxAmount
        ).send({
          from: data.address
        }).then(receipt => {
          toast0.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
          console.log(receipt)
        }).catch(err => {
          toast0.clear()
          Toast(data.$t('management.保存失敗'))
          console.error(err)
        })
      },
      setRate: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.rate) {
          Toast(data.$t('management.請輸入手續費占比'))
          return
        }
        if (Number(data.rate) < 0) {
          Toast(data.$t('management.最小數量需大於0'))
          return
        }
        if (Number(data.rate) > Number(data.denominator)) {
          Toast(data.$t('management.手續費占比不能大於') + data.denominator)
          return
        }
        const toast2 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.seeleContract.methods.setRate(
          data.rate
        ).send({
          from: data.address
        }).then(receipt => {
          toast2.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
          console.log(receipt)
        }).catch(err => {
          toast2.clear()
          Toast(data.$t('management.保存失敗'))
          console.error(err)
        })
      },
      setReceiveAddress: async () => {
        if (!data.address) {
          Toast(data.$t('management.請先連接錢包'))
          return
        }
        if (!data.recivedAddress) {
          Toast(data.$t('management.輸入接收地址'))
          return
        }
        const toast1 = Toast.loading({
          duration: 0,
          forbidClick: true,
          message: '保存中...',
        });
        await data.seeleContract.methods.setReceiveAddress(
          data.recivedAddress
        ).send({
          from: data.address
        }).then(receipt => {
          toast1.clear()
          Toast(data.$t('management.保存成功'))
          data.initData()
          console.log(receipt)
        }).catch(err => {
          toast1.clear()
          Toast(data.$t('management.保存失敗'))
          console.error(err)
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less">
.zui-management {
  .van-cell {
    background: #F9F9F9;
    border-radius: 10px;
  }
}
</style>
<style lang="less" scoped>
.index-bg {
  background: url(~@/assets/images/index-bg.jpg) no-repeat center top 46px;
  background-size: 100% auto;
  font-family: PingFang SC;
  .manage-box {
    margin-top: 15px;
    box-sizing: border-box;
    padding: 0 15px 20px;
    background: #FFFFFF;
    box-shadow: -1px 2.5px 10px 0px rgba(191, 211, 239, 0.38);
    border-radius: 10px;
    .manage-title {
      font-size: 14px;
      font-weight: bold;
      color: #111111;
      line-height: 1;
      padding: 17.5px 0 12px;
    }
  }
}
</style>
